.thumb {
  width: 100px;
  height: 68px;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media screen and (min-width: 768px) {
  .thumb {
    width: 124px;
    height: 88px;
  }
}

@media screen and (min-width: 1280px) {
  .thumb {
    width: 146px;
    height: 104px;
  }
}

.thumb > img {
  object-fit: cover;
}
