.li {
  font-family: 'Manrope';
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  display: flex;
  flex-direction: column;
  margin-bottom: 12px;

  cursor: pointer;

  color: black;
  transition: color 250ms linear;
}

@media screen and (min-width: 768px) {
  .li {
    font-size: 14px;
    line-height: calc(19 / 14);
  }
}

@media screen and (min-width: 1280px) {
  .li {
    font-size: 16px;
    line-height: calc(22 / 16);
  }
}
