.friendList {
  display: flex;
  flex-direction: column;
}
@media screen and (min-width: 768px) {
  .friendList {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 32px;
  }
}
@media screen and (min-width: 1280px) {
  .friendList {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 32px;
  }
}

.item {
  width: 280px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
  background: #ffffff;
  box-shadow: 3px 8px 14px rgba(136, 198, 253, 0.19);
  border-radius: 40px;
  padding: 16px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .item {
    width: 336px;
    flex-basis: calc((768px- 32px) / 2);
  }
}
@media screen and (max-width: 1279px) {
  .item {
    width: 336px;
    flex-basis: calc((768px- 32px) / 2);
  }
}

.content {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  column-gap: 12px;
}
